import React from 'react';
import { createGlobalStyle } from 'styled-components';

import Font_L from './fonts/NotoSerifKR-Light.woff'
import Font_R from './fonts/NotoSerifKR-Regular.woff'
import Font_B from './fonts/NotoSerifKR-Bold.woff'

export default createGlobalStyle`
    @font-face {
        font-family: "Font_L";
        src: local("Font_L"), url(${Font_L}) format('woff'); 
        font-weight: lighter;
    }
    @font-face {
        font-family: "Font_R";
        src: local("Font_R"), url(${Font_R}) format('woff');
        font-weight: normal;
    }
    @font-face {
        font-family: "Font_B";
        src: local("Font_B"), url(${Font_B}) format('woff');
        font-weight: bold;
    }
`